import dummyLetter from "../letterbox/dummy/dummyLetter.json";
import { useNavigate } from "react-router-dom";

const LetterClose = () => {
  let currUrl = window.document.location.href;
  let urlArr = currUrl.split("/");
  let letterId = parseInt(urlArr[urlArr.length - 1]);

  let letters = dummyLetter.letters;
  let currLetter;

  letters.map((l) => {
    if (l.id === letterId) {
      currLetter = l;
    }
  });

  // 
  let url = document.location.href;
  const onShareClick = () => {
    let textArea = document.createElement("textarea");
    document.body.appendChild(textArea);
    textArea.value = url;
    textArea.select();
    document.execCommand("copy");
    document.body.removeChild(textArea);
    alert(" .");
  };

  //  
  const navigate = useNavigate();
  const handleGoBack = () => {
    navigate(-1);
  };

  return (
    <>
      <h1>  ..</h1>
      <button onClick={handleGoBack}> </button>
      {currLetter.sender === currLetter.recipient ? (
        <></>
      ) : (
        <button onClick={onShareClick}> </button>
      )}

      <br />
      <h4> D - {currLetter.dday}</h4>
      {currLetter.sender === currLetter.recipient ? (
        <>
          <p>
            {currLetter.sendAt[0]} {currLetter.sendAt[1]}{" "}
            {currLetter.sendAt[2]}      
            .
          </p>
          <p>   !</p>
        </>
      ) : (
        <>
          <p>
            {currLetter.sendAt[0]} {currLetter.sendAt[1]}{" "}
            {currLetter.sendAt[2]},
          </p>
          <p>
            {currLetter.sender}  {currLetter.recipient}   
               .
          </p>
          <p>
                   !
          </p>
          <form>
            <input type="email" />
            <button type="submit"></button>
          </form>
        </>
      )}
    </>
  );
};

export default LetterClose;
